export default {
	onLoad() {
		uni.showModal({
		    title: '未登录',
		    content: '您未登录，需要登录后才能继续',
			showCancel: false,
		    success: (res) => {
		        if (res.confirm) {
					uni.reLaunch({
					    url: '../registLogin/registLogin'
					});
		        }
		    }
		});
	},
	methods: {
		contacts: function (e) {
			uni.showModal({
				title: "联系人",
				content: "联系人请从客户管理进入",
				showCancel: false,
				confirmText: "确定"
			})
		},
		
		contractManagement: function (e) {
			uni.showModal({
				title: "合同管理",
				content: "测试版，需根据具体情况录入模板",
				showCancel: false,
				confirmText: "确定"
			})
		},
		
		paymentManagement: function (e) {
			uni.showModal({
				title: "回款管理",
				content: "测试版，需根据具体情况录入模板",
				showCancel: false,
				confirmText: "确定"
			})
		},
	}
}
